@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
/*** Main CSS ***/

:root{
  --primary-white: #fff;
  --primarty-dark: rgba(255, 255, 255, 0);

  --primary-orange: #f9ab00;

  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
}

body {
  font-family: 'Roboto Condensed', sans-serif !important ;
}

.section-heading {
  padding-top: 3rem;
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
  padding-bottom: 3rem;
}

.section-border {
  border-style: 2px solid #f9ab00;
  border-style: 2px solid var(--primary-orange);
}


/* Custom CSS */
.padding-left-2 {
  padding-left: 2rem;
}
/**************** Header CSS **************/

nav {
	z-index: 2;
}
  
nav a.nav-link {
	font-weight: 100;
	letter-spacing: 0.0375rem;
	cursor: pointer;
}
  
nav ul li {
	text-transform: uppercase;
}
  
.navbar-nav > li > a {
	margin-right: 0.75rem;
	margin-left: 0.75rem;
	color: var(--primary-white) !important;
	border-top: 0.125rem solid var(--primarty-dark) !important;
}
  
a.navbar-brand {
	font-weight: bold;
	font-size: x-large;
	max-width: 17.25rem;
	padding: 0.5rem !important;
	color: var(--primary-white) !important;
}
  
.navbar-nav > li > a.active, .navbar-nav > li > a:focus {
	border-top: 0.125rem solid var(--primary-orange) !important;
}
  
.navbar-nav > li > a:hover {
	border-top: 0.125rem solid var(--primary-orange) !important;
}


/**************** 2 - INTRO **************/
.intro-wrapper {
	position: relative;
	background: url(/Portfolio/static/media/MainBackground.d6c15d1a.jpg);
	background-size: cover;
	background-position: center;
	height: 90vh;
}

.intro-info {
	display: flex;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0; 
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	z-index: 2;
}


/**************** 2.1 - INTRO HEADING **************/
.intro-info h1 {
	color: #f9ab00;
	text-transform: uppercase;
}



/**************** Areas Of Interest **************/
.areas-of-interest {
	font-size: 2rem;
	color: var(--primary-white);
	padding-bottom: 3rem;
}


/**************** Resume **************/
.intro-info .my-resume {
	text-decoration: underline;
	text-transform: uppercase;
	color: var(--primary-white);
	font-size: x-large;
}



/**************** Contact Me **************/
.intro-contact-me {
	text-decoration: none;
	text-transform: uppercase;
	color: var(--primary-white);

	border: 2px solid red;
	border-radius: 0;
	padding: 0.625rem 0.75rem 0.75rem 0.75rem;
	margin: 2rem;
}

.intro-contact-me:hover {
	text-decoration: none;
	background-color: var(--primary-red);
	color: var(--primary-white);
	-webkit-animation: enable 0.3s linear;
	        animation: enable 0.3s linear;
}

@-webkit-keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@media(max-width: 768px) {
	.intro-info h1 {
		font-size: 1.7rem !important;
	}
	.areas-of-interest {
		font-size: 1.3rem !important;
	}
}


/**************** LinkedIn / GitHub / Gmail Section **************/
/*.social-media-section{
	background-color: #fff;
	padding: 0.5rem;
}*/

.linkedin-logo, .github-logo, .gmail-logo {
	width: 3.5rem;
	background-color: #fff;
}

.linkedin-logo:hover, .github-logo:hover, .gmail-logo:hover, .my-resume:hover {
	-webkit-animation: enable 0.3s linear;
	        animation: enable 0.3s linear;
}
/**************** ABOUT ME **************/
.AboutMe {
	background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url(/Portfolio/static/media/SectionBackground.e134a9cc.jpg);
}

.about-me-section {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
}

.profile-img {
	width: 18rem;
	border-radius: 50%;
	padding: 0.2rem;
	border: 3px solid var(--primary-orange);
}
  
.profile-img-section {
	text-align: center;
}
  
.about-me-heading {
	text-transform: uppercase;
	color: var(--primary-orange);
}

.about-me-main-text, .about-me-description {
	color: #fff;
}

  
@media(max-width: 768px) {
	.profile-img {
	  width: 10rem;
	}
  
	.about-me-heading {
	  padding-top: 25px;
	}
  
	.about-me-heading, .about-me-description {
	  padding-left: 1rem;
	  padding-right: 1rem;
	  text-align: center;
	}
}

/********************** Background - CSS **********************/

.Background {
	background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url(/Portfolio/static/media/SectionBackground.e134a9cc.jpg);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
}

.Background title {
    position: fixed;
}

/*** Vertical timeline line color ***/
.vertical-timeline::before {
    background: #f9ab00;
}

/*** ***/
span.vertical-timeline-element-date {
    font-weight: bold;
    font-size: 2rem;
    color: #fff;
}
/********************** Skills - CSS **********************/

.Skills {
	background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url(/Portfolio/static/media/SectionBackground.e134a9cc.jpg);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
}

.skills-image-container img {
	overflow: hidden;
	background-color: #fff;
	width: 12rem;
	height: 6rem;
}





/* Programming Section */
.programming, .databases, .frameworks, .web-technologies, .other-technologies {
    color: #fff;
}

.programming-skills, .databases-skills, .frameworks-skills, .web-technologies-skills, .other-technologies-skills {
	display: flex;
	flex-direction: row;
}



@media(max-width: 768px) {
	.skills-image-container img {
		background-color: #fff;
		width: 8rem;
		height: 4rem;
	}

	.programming-skills, .databases-skills, .frameworks-skills, .web-technologies-skills, .other-technologies-skills {
		flex-direction: column;
	}
}
/********************** Card - CSS **********************/

.card-container {
	border: 2px solid #fff;
	overflow: hidden;
	box-shadow: 0rem 0rem 2rem -1rem;
	transition: 0.3s;
	-webkit-animation: ease-in;
	        animation: ease-in;
}

.card-container:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
	box-shadow: 0rem 0rem 2rem 0rem;
	cursor: pointer;
}

.card-title {
	text-align: center;
	color: #fff;
}
/* For Projects */
.Projects {
	background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url(/Portfolio/static/media/SectionBackground.e134a9cc.jpg);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
}

.projects {
	display: flex;
}


.projects-card .projects-image-container img {
	overflow: hidden;
	background-color: #fff;
	width: 18rem;
	height: 12rem;
}

.projects-card .card-body {
	display: flex;
	justify-content: center;
	align-items: center;
	color: #fff;
}

.card-link {
	display: flex;
	justify-content: center;
	align-items: center;
	color: #fff;
}

.card-url, .card-url:hover {
	text-decoration: none;
	color: #fff;
	padding: 0.5rem;
	border: 2px solid #fff;
}

.card-url:hover {
	background-color: #fff;
	color: #000;
	-webkit-animation: enable 0.3s linear;
	        animation: enable 0.3s linear;
}

@-webkit-keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
/*** ContactMe - CSS ***/

.ContactMe {
	background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url(/Portfolio/static/media/SectionBackground.e134a9cc.jpg);
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
}

.ContactMe h4 {
	color: #fff;
}

.contact-me-form {
	padding: 2rem;
}


.contact-me-form input, .contact-me-form textarea {
	width: 100%;
	box-sizing: border-box;
	background: none;
	border: 0;
	border: 1px solid #fff;
	border-radius: 0;
	outline: 0 !important;
	box-shadow: none;
	color: #fff;
}

.contact-me-form input:focus, .contact-me-form textarea:focus {
	background: transparent !important;
	color: #fff;
}

.contact-me-btn, .reset-contact-me-btn{
	width: 75%;
	padding: 1rem;
}

.error-message {
	color: #fff;
	background: red;
	text-align: left;
}

.success-message {
	font-weight: bold;
	color: green;
}
